.header {
    position: fixed;
    width: 98.5%;
    margin: none;
    /* margin: auto; */
    height: 5rem;
    background-color: #8a2b06;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.25);
    z-index: 12;
}

@media only screen and (min-width: 1100px) {
    .header {
        max-width: 1250px;
    }
}

.main-image{
    width: 100%;
    height: 32rem;
    z-index: 0;
    overflow: hidden;
}

.main-image img{
    width: 110%;
    height: 100%;
    object-fit: cover;
    transform: rotate(-5deg) translateY(-4rem) translateX(-1rem);
}